<template>
<div>
	<h2 class="title"></h2>
	<h3 class="sub"></h3>
	<em-rollnotice :speed="speed" :keepmove="keepmove" direction='horizontal'></em-rollnotice>
	<pre>&lt;em-rollnotice :speed="speed" :keepmove="keepmove" direction='horizontal'&gt;&lt;/em-rollnotice&gt;
&lt;script&gt;
export default {
  data (){
    return {
      speed:2,
      keepmove:true
    }
  }
}
&lt;/script&gt;
</pre>

	<h3 class="sub"></h3>
	<em-rollnotice :speed="speed" :line="line"></em-rollnotice>
	<pre>&lt;em-rollnotice :speed="speed" :line="line"&gt;&lt;/em-rollnotice&gt;
&lt;script&gt;
export default {
  data (){
    return {
      speed:2,
      line:2
    }
  }
}
&lt;/script&gt;
</pre>

	<h3 class="sub"></h3>
	<em-rollnotice :speed="speed" :keepmove="keepmove"></em-rollnotice>
	<pre>&lt;em-rollnotice :speed="speed" :keepmove="keepmove"&gt;&lt;/em-rollnotice&gt;
&lt;script&gt;
export default {
  data (){
    return {
      speed:2,
      keepmove:true
    }
  }
}
&lt;/script&gt;
</pre>
	<h3 class="sub"></h3>
	<table class="props" cellpadding="0" cellspacing="0">
		<tr>
			<th></th>
			<th></th>
			<th></th>
			<th></th>
		</tr>
		<tr>
			<td>speed</td>
			<td>Number</td>
			<td>4</td>
			<td></td>
		</tr>
		<tr>
			<td>rolllist</td>
			<td>Array</td>
			<td>['','','','']</td>
			<td></td>
		</tr>
		<tr>
			<td>keepmove</td>
			<td>Boolean</td>
			<td>false</td>
			<td></td>
		</tr>
		<tr>
			<td>line</td>
			<td>Number</td>
			<td>1</td>
			<td></td>
		</tr>
		<tr>
			<td>direction</td>
			<td>String</td>
			<td>vertical</td>
			<td></td>
		</tr>
	</table>
</div>
</template>
<script>
export default {
  data (){
    return {
      speed:2,
      keepmove:true,
      line:2
    }
  },
  methods:{

  },
  mounted(){

  }
}
</script>
<style scoped>

</style>



