	<pre>&lt;em-datepicker :disableback="disableback"&gt;&lt;/em-datepicker&gt;</pre>
	<h3 class="sub"></h3>
	<table class="props" cellpadding="0" cellspacing="0">
		<tr>
			<th></th>
			<th></th>
			<th></th>
			<th></th>
		</tr>
		<tr>
			<td>ismultiple</td>
			<td>String</td>
			<td>single</td>
			<td></td>
		</tr>
		<tr>
			<td>disableback</td>
			<td>Boolean</td>
			<td>false</td>
			<td></td>
		</tr>
	</table>
  </div>
</template>
<script>
export default {
  name: 'datepicker',
  data () {
    return {
      disableback:true,
      disableforward:true
    }
  },
  methods:{

  }
}
</script>
