	<pre>&lt;em-input placeholder=".." v-model="input"&gt;&lt;/em-input&gt;</pre>
	<h3 class="sub"></h3>
    <em-input placeholder="" disabled v-model="input"></em-input>
	<pre>&lt;em-input placeholder="" disabled v-model="input"&gt;&lt;/em-input&gt;</pre>
	<h3 class="sub"></h3>
	<table class="props" cellpadding="0" cellspacing="0">
		<tr>
			<th></th>
			<th></th>
			<th></th>
			<th></th>
		</tr>
		<tr>
			<td>placeholder</td>
			<td>String</td>
			<td></td>
			<td></td>
		</tr>
		<tr>
			<td>disabled</td>
			<td>Boolean</td>
			<td>false</td>
			<td></td>
		</tr>
	</table>
  </div>
</template>

<script>
import emInput from '../components/emInput.vue'
export default {
  name: 'HelloWorld',
  data () {
    return {
      input:''
    }
  },
  components: {
    "em-input" : emInput
  },
  methods:{

  }
}
</script>



