			<td>null</td>
			<td></td>
		</tr>
		<tr>
			<td>checked</td>
			<td>Boolean</td>
			<td>false</td>
			<td></td>
		</tr>
		<tr>
			<td>disabled</td>
			<td>Boolean</td>
			<td>false</td>
			<td></td>
		</tr>
	</table>
  </div>
</template>

<script>
export default {
  name: 'checkbox',
  data () {
    return {
      	
    }
  },
  methods:{
	
  }
}
</script>
