<template>
  <div>
  	<h2 class="title"></h2>
	<h3 class="sub"></h3>
	<em-tab>
      <div>index=0</div>
      <div>index=1</div>
      <div>index=2</div>
    </em-tab>
<pre>
&lt;em-tab&gt;
  &lt;div&gt;index=0&lt;div&gt;
  &lt;div&gt;index=1&lt;div&gt;
  &lt;div&gt;index=2&lt;div&gt;
&lt;/em-tab&gt;
</pre>

	<h3 class="sub"></h3>
    <em-tab :activedIndex="activedIndex" :tabItem="tabItem">
      <div>index=0</div>
      <div>index=1</div>
      <div>index=2</div>
      <div>index=3</div>
      <div>index=4</div>
    </em-tab>
<pre>
&lt;em-tab :activedIndex="activedIndex" :tabItem="tabItem"&gt;
  &lt;div&gt;index=0&lt;div&gt;
  &lt;div&gt;index=1&lt;div&gt;
  &lt;div&gt;index=2&lt;div&gt;
  &lt;div&gt;index=3&lt;div&gt;
  &lt;div&gt;index=4&lt;div&gt;
&lt;/em-tab&gt;
&lt;script&gt;
export default {
  name: 'tab',
  data () {
	return {
	  activedIndex:1,
	  tabItem:['','','','','']
	}
  }
}
&lt;/script&gt;
</pre>

	<h3 class="sub"></h3>
    <em-tab :activedIndex="activedIndex2" :tabItem="tabItem2" type="card">
      <div>index=0</div>
      <div>index=1</div>
      <div>index=2</div>
      <div>index=3</div>
    </em-tab>
<pre>
&lt;em-tab :activedIndex="activedIndex2" :tabItem="tabItem2" type="card"&gt;
  &lt;div&gt;index=0&lt;div&gt;
  &lt;div&gt;index=1&lt;div&gt;
  &lt;div&gt;index=2&lt;div&gt;
  &lt;div&gt;index=3&lt;div&gt;
  &lt;div&gt;index=4&lt;div&gt;
&lt;/em-tab&gt;
&lt;script&gt;
export default {
  name: 'tab',
  data () {
	return {
	  activedIndex2:1,
	  tabItem2:['','','','']
	}
  }
}
&lt;/script&gt;
</pre>

  	<h3 class="sub"></h3>
    <em-tab triggerType="mouseover" :activedIndex="activedIndex2" :tabItem="tabItem2" type="card">
      <div>index=0</div>
      <div>index=1</div>
      <div>index=2</div>
      <div>index=3</div>
    </em-tab>
<pre>
&lt;em-tab triggerType="mouseover" :activedIndex="activedIndex2" :tabItem="tabItem2" type="card"&gt;
  &lt;div&gt;index=0&lt;div&gt;
  &lt;div&gt;index=1&lt;div&gt;
  &lt;div&gt;index=2&lt;div&gt;
  &lt;div&gt;index=3&lt;div&gt;
  &lt;div&gt;index=4&lt;div&gt;
&lt;/em-tab&gt;
&lt;script&gt;
export default {
  name: 'tab',
  data () {
	return {
	  activedIndex2:1,
	  tabItem2:['','','','']
	}
  }
}
&lt;/script&gt;
</pre>

	<h3 class="sub"></h3>
	<table class="props" cellpadding="0" cellspacing="0">
		<tr>
			<th></th>
			<th></th>
			<th></th>
			<th></th>
		</tr>
		<tr>
			<td>activedIndex</td>
			<td>Number</td>
			<td>0</td>
			<td></td>
		</tr>
		<tr>
			<td>tabItem</td>
			<td>Array</td>
			<td>['','','']</td>
			<td></td>
		</tr>
		<tr>
			<td>type</td>
			<td>String</td>
			<td>default</td>
			<td></td>
		</tr>
		<tr>
			<td>triggerType</td>
			<td>String</td>
			<td>click</td>
			<td></td>
		</tr>
	</table>
  </div>
</template>
<script>
export default {
  name: 'tab',
  data () {
    return {
      activedIndex:1,
      tabItem:['','','','',''],
      activedIndex2:1,
      tabItem2:['','','','']
    }
  }
}
</script>
<style lang="scss" scoped>

</style>



